#root{
  display: flex;
  width: 100%;
  height: 100%;
}
.App{
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: flex-start;
  flex-direction: column;
  gap: 20px;
}
body{
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100vh;
}

.bgImage{
  width: 100%;
  position: absolute;
  height: 100vh;
  object-fit: cover;
  object-position: top center;
}
.content{
  margin-top: 100px;
  position: relative;
  z-index: 10;
  background: lightblue;
  border-radius: 12px;
  border: 1px solid lightslategray;
  padding: 20px;
  margin-left: 20px;
  margin-right: 20px;
}
.contentOutput{
  margin-top: 20px;
  padding: 10px;
  background: rgb(212, 244, 255);
  border-radius: 12px;
  border: 1px solid lightslategray;
}
h1{
  font-size: 24px;
  color: black;
}
h2{
  font-size: 18px;
}
.inputWrapper{
  display: flex;
  gap: 20px;
  width: 100%;
  justify-content: space-between;
}
input{
  border-radius: 8px;
  background: rgb(212, 244, 255);
  border: 1px solid lightslategray;
padding: 10px;
width: 100%;

}
button{
  border-radius: 8px;
  background: rgb(212, 244, 255);
  border: 1px solid lightslategray;
padding: 10px;
}

p{
  margin-top: 0;
}